<template>
  <div class="order">
    <Breadcrumb name1="" name2="" />
    <el-card>
      <!--      -->
      <el-row>
        <el-col :span="8">
          <el-input placeholder="" clearable v-model="queryInfo.query" @clear="getOrderList">
            <el-button slot="append" icon="el-icon-search" @click="getOrderList"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <!--      -->
      <el-row>
        <el-table :data="orderList" border style="width: 100%">
          <el-table-column type="index" label="#"> </el-table-column>
          <el-table-column prop="order_number" label=""></el-table-column>
          <el-table-column prop="order_price" label="" width="250px"></el-table-column>
          <el-table-column label="" width="250px">
            <template v-slot="scope">
              <el-tag type="danger" v-if="scope.row.pay_status === '0'"></el-tag>
              <el-tag type="success" v-else></el-tag>
            </template>
          </el-table-column>
          <el-table-column prop="is_send" label="" width="250px"></el-table-column>
          <el-table-column label="" width="250px">
            <template v-slot="scope">
              {{ scope.row.create_time | dataFormat }}
            </template>
          </el-table-column>
          <el-table-column label="" width="150px">
            <template v-slot="">
              <el-tooltip class="item" effect="dark" content="" placement="top">
                <el-button type="primary" icon="el-icon-edit" circle @click="showBox"></el-button>
              </el-tooltip>
              <el-tooltip class="item" effect="dark" content="" placement="top">
                <el-button type="success" icon="el-icon-location" circle @click="showProgressBox"></el-button>
              </el-tooltip>
            </template>
          </el-table-column>
        </el-table>
      </el-row>
      <!--      -->
      <el-row>
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="queryInfo.pagenum"
          :page-sizes="[5, 9, 11, 15]"
          :page-size="queryInfo.pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        >
        </el-pagination>
      </el-row>
    </el-card>
    <!--    -->
    <el-dialog title="" :visible.sync="addressDialogVisible" @close="addressDialogClosed" width="50%">
      <el-form :model="addressForm" :rules="addressFormRules" ref="addressFormRef" label-width="100px">
        <el-form-item label="/" prop="address1">
          <!--          <el-input v-model="addressForm.address1"></el-input>-->
          <el-cascader :options="cityData" v-model="addressForm.address1" :props="{ expandTrigger: 'hover' }"></el-cascader>
        </el-form-item>
        <el-form-item label="" prop="address2">
          <el-input v-model="addressForm.address2"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addressDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addressDialogVisible = false"> </el-button>
      </span>
    </el-dialog>
    <!--    -->
    <el-dialog title="" :visible.sync="progressDialogVisible" width="50%">
      <el-timeline :reverse="false">
        <el-timeline-item v-for="(activity, index) in progressInfo" :key="index" :timestamp="activity.ftime">
          {{ activity.context }}
        </el-timeline-item>
      </el-timeline>
    </el-dialog>
  </div>
</template>

<script>
import cityData from './citydata'
import Breadcrumb from 'components/content/breadcrumb/Breadcrumb'
export default {
  name: 'Order',
  components: {
    Breadcrumb
  },
  data() {
    return {
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 7
      },
      // 
      orderList: [],
      // 
      total: 0,
      // 
      addressDialogVisible: false,
      addressForm: {
        address1: [],
        address2: ''
      },
      addressFormRules: {
        address1: [{ required: true, message: '/', trigger: 'blur' }],
        address2: [{ required: true, message: '', trigger: 'blur' }]
      },
      // 
      cityData,
      // 
      progressDialogVisible: false,
      progressInfo: []
    }
  },
  created() {
    this.getOrderList()
  },
  methods: {
    // 
    async getOrderList() {
      const { data: res } = await this.$http.get('orders', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) {
        return this.$message.error('!')
      }
      this.$message.success('!')
      this.orderList = res.data.goods
      this.total = res.data.total
      console.log(res, this.orderList, this.total)
    },
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getOrderList()
    },
    handleCurrentChange(newCurrent) {
      this.queryInfo.pagenum = newCurrent
      this.getOrderList()
    },
    // 
    showBox() {
      this.addressDialogVisible = true
    },
    // 
    addressDialogClosed() {
      this.$refs.addressFormRef.resetFields()
    },
    async showProgressBox() {
      const { data: res } = await this.$http.get('/kuaidi/1106975712662')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.progressInfo = res.data

      // this.progressInfo = [
      //   {
      //     time: '2018-05-10 09:39:00',
      //     ftime: '2018-05-10 09:39:00',
      //     context: ',,',
      //     location: ''
      //   },
      //   {
      //     time: '2018-05-10 08:23:00',
      //     ftime: '2018-05-10 08:23:00',
      //     context: '[]  95338',
      //     location: ''
      //   },
      //   {
      //     time: '2018-05-10 07:32:00',
      //     ftime: '2018-05-10 07:32:00',
      //     context: ' []',
      //     location: ''
      //   },
      //   {
      //     time: '2018-05-10 02:03:00',
      //     ftime: '2018-05-10 02:03:00',
      //     context: '[], []',
      //     location: ''
      //   },
      //   {
      //     time: '2018-05-09 23:05:00',
      //     ftime: '2018-05-09 23:05:00',
      //     context: ' []',
      //     location: ''
      //   },
      //   {
      //     time: '2018-05-09 21:21:00',
      //     ftime: '2018-05-09 21:21:00',
      //     context: '[], []',
      //     location: ''
      //   },
      //   {
      //     time: '2018-05-09 13:07:00',
      //     ftime: '2018-05-09 13:07:00',
      //     context: ' ',
      //     location: ''
      //   },
      //   {
      //     time: '2018-05-09 12:25:03',
      //     ftime: '2018-05-09 12:25:03',
      //     context: '',
      //     location: ''
      //   },
      //   {
      //     time: '2018-05-09 12:22:24',
      //     ftime: '2018-05-09 12:22:24',
      //     context: 'HLA',
      //     location: ''
      //   },
      //   {
      //     time: '2018-05-08 21:36:04',
      //     ftime: '2018-05-08 21:36:04',
      //     context: '',
      //     location: ''
      //   }
      // ]
      //
      this.progressDialogVisible = true
      //   console.log(this.progressInfo)
    }
  }
}
</script>

<style scoped>
.el-table {
  margin: 15px 0 5px;
}
.el-cascader {
  width: 100%;
}
</style>
